import React, { useState } from "react"

function FetchAPI() {

    const [data, setData] = useState()

    const apiGet = () => {
        // fetch('https://reqres.in/api/users?page=1')      // for the page 1
        fetch('https://reqres.in/api/users?page=2')         // for the page 2

            .then((response) => response.json())
            .then((json) => {
                console.log(json)
                setData(json);
            });

    }

    return (
        <div>
        <h1> Click the below button to fetch the API </h1>
            <button onClick= {apiGet} > Fetch API </button>
            <br />
            <pre>{JSON.stringify(data,null, 2)}</pre>    
        <div> 
        {/* <ul>
          {data.map((item) => (
            <li key={item.id}>
              {item.name}
            </li>
            <li>{item.email} </li>
            <li>{item.first_name} </li>
            <li>{item.last_name} </li>
            <li>{item.avatar} </li>

          ))}
        </ul> */}
        
        </div>
        
        
        
        </div>
    );

}
export default FetchAPI;